.edgeButton {
  cursor: pointer;
  pointer-events: all;
  stroke: #f0f0f1;
  fill: #f0f0f1;
}

.edgeButtonText {
  pointer-events: none;
  user-select: none;
  fill: #0d6efd;
  font-size: 32px;
  font-weight: 400;
}

.edgePath {
  fill: none;
  stroke: #0d6efd;
  stroke-width: 1;
  pointer-events: none;
}

.placeholderPath {
  stroke-width: 1;
  stroke-dasharray: 3 3;
  stroke: #0a84e3;
  fill: none;
}
