.node {
  border: 1px solid #e8e7e4;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 5px 10px 0px;
  /* padding: 10px; */
  width: 350px;
  text-align: center;
  font-weight: bold;
  color: rgb(45, 46, 46);
  cursor: pointer;
  line-height: 1.2;
  transition: opacity 150ms ease-out, box-shadow 150ms ease-out, border-color 300ms ease-in-out, background-color 300ms ease-in-out;
}
.node:hover {
  border-color: #0d6efd;
}

.addStepNode {
  border-radius: 4px;
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 5px 10px 0px;
  transition: opacity 150ms ease-out, box-shadow 150ms ease-out, border-color 300ms ease-in-out, background-color 300ms ease-in-out;
  padding: 12px;
  width: 350px;
  text-align: center;
  font-weight: bold;
  color: rgb(45, 46, 46);
  cursor: pointer;
  line-height: 1.2;
}
.skippedNode {
  border-color: #FF9800 !important;
  box-shadow: rgb(255 193 7 / 15%) 0px 5px 10px 0px !important;
}

.skippedNode .stepIconWrapper {
  background-color: #FFF3E0;
}

.stepHeader {
  display: flex;
  width: 100%;
  gap: 8px;
  -webkit-box-align: start;
  align-items: center;
  justify-content: space-between;
}
:global .step-index {
  display: flex;
  gap: 5px;
  align-items: center;
}
.stepAppLabel {
  font-size: 14px;
  font-weight: 700;
  font-family: 'Open Sans';
  pointer-events: none;
}
.stepEventLabel {
  font-weight: 500;
  word-break: break-word;
  max-width: 250px;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  width: 100%;
  font-size: 15px;
  text-align: left;
  pointer-events: none;
}
.actionStep {
  border: 1px solid rgb(241, 247, 255);
  overflow: hidden;
}
.stepIconWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgb(241, 247, 255);
  padding: 18px;
  pointer-events: none;
}
.stepInfo {
  position: relative;
  display: flex;
  width: 100%;
  max-width: 350px;
  overflow: hidden;
  gap: 8px;
  flex-direction: column;
  padding: 12px 10px;
  pointer-events: none;
}
.actionStepNew {
  border: 1px dashed #323232;
}
.appStepActions svg {
  pointer-events: none;
}
.appStepActions .nodeActionIcon {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid transparent;
  border-radius: 3px;
  width: 24px;
  height: 24px;
  transition: all 0.2s ease-in-out;
}
.appStepActions .nodeActionIcon:hover {
  background-color: rgb(241, 247, 255);
  border-color: #a4c4ff;
  color: #0d6efd;
  transition: all 0.2s ease-in-out;
}
.appStepActions,
.appRouteActions {
  display: flex;
  flex-direction: column;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: justify;
  justify-content: space-between;
  height: 50px;
  padding-right: 6px;
}
.appRouteActions {
  height: auto;
}
.appRouteActions svg {
  pointer-events: none;
}
.appActionEventsToggle {
  display: flex;
  margin: 0px;
  width: 24px;
  height: 24px;
}
.toggleRouteMenu,
.toggleStepMenu {
  position: relative;
  display: flex;
  opacity: 1;
  background-color: transparent;
  color: rgb(45, 46, 46);
  border: 1px solid transparent;
  border-radius: 2px;
  height: 24px;
  width: inherit;
  -webkit-box-align: center;
  align-items: center;
  -webkit-box-pack: center;
  justify-content: center;
  transition: border-color 300ms ease-in-out, opacity 300ms ease-in-out;
  flex-direction: column;
}

.toggleRouteMenu {
  color: #0d6efd;
}

.toggleRouteMenu svg,
.toggleStepMenu svg {
  width: 20px;
  height: 20px;
  display: block;
  pointer-events: none;
}

.content {
  pointer-events: none;
}

.handle {
  visibility: hidden;
}
.routerSwitch {
  visibility: visible;
  background: transparent;
  box-shadow: none;
  border: none;
  width: auto;
  transform: translateX(160px);
  position: relative;
  padding: 0;
  box-shadow: none !important;
}
.routerSwitch:before {
  border: none !important;
}
.routerRoute {
  background: transparent;
  box-shadow: none;
  border: none;
  width: 350px;
  max-width: 350px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0;
}
.routerRouteHandle {
  display: flex;
  justify-content: space-between;
  background: #fff;
  color: #0d6efd;
  border-radius: 50px;
  background-color: #fff;
  box-shadow: rgba(0, 0, 0, 0.1) 0px 5px 10px 0px;
  transition: opacity 150ms ease-out, box-shadow 150ms ease-out, border-color 300ms ease-in-out, background-color 300ms ease-in-out;
  padding: 8px 18px;
  max-width: inherit;
  border: 2px solid #79a8ff;
  align-items: center;
}
.routerRouteLabel {
  max-width: max-content;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  line-height: 24px;
  padding-right: 10px;
}
.routerRouteActions {
  display: flex;
  align-items: center;
  height: 24px;
  margin-left: 10px;
  position: relative;
  left: 10px;
  gap: 5px;
}
.routerRouteCollapse {
  width: 24px;
}
.routerRouteCollapse svg {
  transition: all 0.2s ease-in-out;
  pointer-events: none;
}
.routerStepsCollapsed {
  position: relative;
}
.routerStepsCollapsed svg {
  transform: rotate(180deg);
}
.placeholder {
  width: 350px;
  background: transparent;
  border: none;
  box-shadow: none;
}
:global .react-flow__node.react-flow__node-placeholder {
  top: -20px;
}
.addStepButton {
  background: #f0f0f1;
  border: none;
  font-size: 32px;
  font-weight: 400;
  box-shadow: none;
  width: 30px;
  margin: 0 auto;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #0d6efd;
}
.alignCenter {
  display: flex;
  align-items: center;
}
:global .node-selected {
  border-color: #0d6efd;
  background-color: #f1f7ff;
  box-shadow: rgba(0, 0, 0, 0.2) 0px 10px 30px 0px;
  border-radius: 5px;
}
:global .node-selected:before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  border: 2px solid #0d6efd;
  border-radius: 4px;
}

:global :root {
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  line-height: 1.5;
  font-weight: 400;
}

:global html,
:global body,
:global #root {
  height: 100%;
  margin: 0;
}

:global body.ReactModal__Body--open {
  overflow: hidden;
}

:global #workflow-builder-root {
  position: fixed;
  height: calc( 100vh - 112px );
  width: calc(100% - var(--adminmenuwrap-width));
  right: 0;
  left: calc(var(--adminmenuwrap-width) + 60px);
  top: 110px;
}

:global .css-m_bc4::before {
  position: absolute;
  content: "";
  margin: -1px;
  inset: 0px;
  border: 2px solid transparent;
  transition: margin 200ms ease-in-out, border-color 200ms ease-in-out;
  border-radius: 4px;
}
:global .selected .css-m_bc4::before {
  border: 2px solid #0d6efd;
  transition: margin 200ms ease-in-out, border-color 200ms ease-in-out;
}
:global .bg-none {
  background-color: transparent !important;
}
:global .flowmattic-wrap .flowmattic-workflow-header {
  max-width: calc(100% - var(--adminmenuwrap-width));
}
:global .folded .flowmattic-wrap .flowmattic-workflow-header {
  max-width: calc(100% - 40px);
}
:global .folded #workflow-builder-root {
  left: 100px;
}
:global .ReactModal__Overlay {
  z-index: 99999 !important;
}