:root {
  font-family: Inter, system-ui, Avenir, Helvetica, Arial, sans-serif;
  line-height: 1.5;
  font-weight: 400;
}

html,
body,
#root {
  height: 100%;
  margin: 0;
}

body.ReactModal__Body--open {
  overflow: hidden;
}

#workflow-builder-root {
  position: fixed;
  height: calc( 100vh - 112px );
  width: calc(100% - var(--adminmenuwrap-width));
  right: 0;
  left: calc(var(--adminmenuwrap-width) + 60px);
  top: 110px;
}

.css-m_bc4::before {
  position: absolute;
  content: "";
  margin: -1px;
  inset: 0px;
  border: 2px solid transparent;
  transition: margin 200ms ease-in-out, border-color 200ms ease-in-out;
  border-radius: 4px;
}
.selected .css-m_bc4::before {
  border: 2px solid #0d6efd;
  transition: margin 200ms ease-in-out, border-color 200ms ease-in-out;
}
.bg-none {
  background-color: transparent !important;
}
.flowmattic-wrap .flowmattic-workflow-header {
  max-width: calc(100% - var(--adminmenuwrap-width))
}
.folded .flowmattic-wrap .flowmattic-workflow-header {
  max-width: calc(100% - 40px);
}
.folded #workflow-builder-root {
  left: 100px;
}
.ReactModal__Overlay {
  z-index: 99999 !important;
}