.actionMenu {
	position: fixed;
	background: white;
	border: 1px solid #ccc;
	border-radius: 4px;
	box-shadow: rgba(0, 0, 0, 0.2) 0px 10px 30px 0px;
	width: 200px;
	z-index: 9999;
	text-align: left;
}

.actionMenu .actionMenuList {
	list-style-type: none;
	margin: 0;
	padding: 2px;
}

.actionMenu .actionMenuItem {
	padding: 10px 15px;
	cursor: pointer;
	color: #333;
	transition: background 0.3s ease;
	font-weight: 500;	
	margin: 0;
}

.actionMenuItem {
	position: relative;
	display: flex;
    -webkit-box-align: center;
    align-items: center;
    gap: 10px;
}

.actionMenu .actionMenuItem:hover {
	background-color: rgb(241, 247, 255);
	color: #0d6efd;
}

.actionMenu .actionMenuItem.disabled {
	color: #aaa;
	cursor: not-allowed;
}

.deleteActionName {
	position: relative;
}
.actionMenu .actionMenuItem.actionMenuItemDelete.actionMenuItemFocus,
.actionMenu .actionMenuItem.actionMenuItemDelete:hover {
	color: #f24f2e;
    background-color: #ffe4e7;
}

.actionMenu .actionMenuItemIcon {
	display: flex;;
}

.actionMenuItem.actionMenuItemFocus .actionMenuItemIconDefault,
.actionMenuItem:hover .actionMenuItemIconDefault {
	display: none;
}

.actionMenuItem .actionMenuItemIconHover {
	display: none;
}

.actionMenuItem.actionMenuItemFocus .actionMenuItemIconHover,
.actionMenuItem:hover .actionMenuItemIconHover {
	display: flex;
}

.nodeActionTooltip {
	position: absolute;
	z-index: 4;
    margin-left: -10px;
}
.tooltipWrapper {
	visibility: hidden;
	max-width: 300px;
    width: max-content;
    right: 0px;
    position: absolute;
}
.tooltipWrapper[data-show-tooltip="true"] {
	visibility: visible;
	transition: 0.3s 0.5s;
}
.nodeActionTooltipContent {
	background: #333;
	color: white;
	padding: 12px 14px;
    border-radius: 4px;
    font-size: 15px;
    font-weight: 500;
    max-width: 200px;
}
.animateSlideIn {
	animation:animateSlideIn 0.4s
}
@keyframes animateSlideIn {
	0% {
		opacity: 0;
		transform: translateY(15px);
	}
	100% {
		opacity: 1;
		transform: translateY(0);
	}
}